// 导出 UnoCSS 配置
export { sectumUnoConfig } from './sectum-uno.config'


// 导出配置说明
export const configInfo = {
  name: 'Sectum UI UnoCSS Configuration',
  version: '0.1.2',
  description: 'UnoCSS configuration for Sectum UI component library',
  usage: {
    basic: `
// 在你的 uno.config.js 中
import { defineConfig } from 'unocss'
import { sectumUnoConfig } from 'sectum'

export default defineConfig({
  ...sectumUnoConfig,
  // 你的其他配置
})
    `,
    advanced: `
// 高级用法 - 扩展配置
import { defineConfig } from 'unocss'
import { presetUno, presetAttributify, presetIcons } from 'unocss'
import { sectumUnoConfig } from 'sectum'

export default defineConfig({
  presets: [
    presetUno(),
    presetAttributify(),
    presetIcons(),
    ...sectumUnoConfig.presets
  ],
  rules: [
    ...sectumUnoConfig.rules,
    // 你的自定义规则
  ],
  safelist: [
    ...sectumUnoConfig.safelist,
    // 你的其他 safelist 类名
  ],
  theme: {
    ...sectumUnoConfig.theme,
    // 你的主题扩展
  }
})
    `
  },
  features: [
    '支持 sectum 组件的所有颜色变量',
    '自动生成必要的 CSS 类名',
    '支持主题切换和深色模式',
    '支持透明度和悬停状态',
    '优化的 safelist 配置',
    'HarmonyOS 字体支持'
  ]
}
